@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');


*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body{
  font-family: 'Bebas Neue', cursive;
  background-color: #fce5cd ;
}

.container{
  margin: 30px auto;
  max-width: 400px;
  min-height: 300px;
  overflow: auto;
  border: 1px solid rgb(8,56,96);
  padding: 30px;
  border-radius: 10px;
  background-color:#f7d7b5
  
}

.header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}


.btn {
  background: rgb(3, 3, 64);
  display: inline-block;
  color: #fff;
  padding: 10px 20px;
  border:none;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus{
  outline:none;
}

.btn:active{
  transform: scale(1.05);
}

.btn--block{
  display: block;
  width: 100%;
}

.task{
  background: #aba6a6;
  margin: 5px;
  padding: 12px 20px;
  cursor:pointer;
} 

.task.activeReminder{
  border-left: 10px solid rgb(6, 73, 6);
}

.task__heading--3{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

p{
  padding: 0 30px;
}


/*******************ADD TASK****************/

.form--add{
  margin-bottom: 40px;
}

.form__controller{
  margin:20px 0;
}

.form__label{
  display: block;
}

.form__input{
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.form__controller--check{
  display:flex;
  align-items:center;
  justify-content: space-between;
}

.form__label--check{
  flex:1;
}

.form__input--check{
  flex:2;
  height: 20px;
}